.projects-wrapper {
    min-height: 115vh;
    background-color: #647C90;
    color: #ede6df;
    font-family: 'IBM Plex Mono', monospace;
}

.projects-title {
    padding: 6rem;
    padding-bottom: 3rem;
}

.project {
    background-color: #ede6df;
    color: black;
    height: 100%;
}

@media (max-width: 767px) {
    .project {
        background-color: #ede6df;
        color: black;
        height: auto;
    }
}