/* styles.css (or any other stylesheet) */

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: #746C70;;
    color: #ede6df;
    position: fixed;
    height: 4rem;
    width: 100%;
    top: -100px;
    transition: top 0.3s ease-in-out;
    font-family: 'IBM Plex Mono', monospace;
    z-index: 100;
  }
  
  .navbar.sticky {
    top: 0;
  }
  
  .navbar-left {
    display: flex;
    align-items: center;
  }
  
  .navbar-right {
    display: flex;
  }
  
  .navbar-name {
    font-size: 1.5em;
    margin-right: 20px;
    font-weight: bold;
  }
  
  .navbar-link {
    margin-right: 20px;
    color: white;
    text-decoration: none;
  }

  .navbar a {
    text-decoration: none;
    color: #ede6df;
  }
  
  /* Add hover effect if needed */
  .navbar-link:hover {
    text-decoration: underline;
  }

  /* Media query for smaller devices */
  @media (max-width: 767px) {
    .navbar-link {
      display: none; /* Hide links on smaller devices */
    }
  }